/**
  get
  token,
*/
export function getByCheck(url, params) {
  let token = localStorage.getItem('token');
  if(token == null){
    Toast.fail('');
    router.push('/login');
    return
  }else{
    return axios({
      method: 'get',
      url,
      params, // get 
      timeout: 5000
    })
  } 
}

/**
 * post key=val&key=val
 * token,
*/
export function postByCheck(url, data) {
  let token = localStorage.getItem('token');
  if(token == null){
    Toast.fail('');
    router.push('/login');
  }else{
    return axios({
      method: "post",
      url,
      data: qs.stringify(data),
      timeout: 5000,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    })
  }
}

/**
 * post ,
 * token,
*/
export function post_obj_arrayByCheck(url, data) {
  let token = localStorage.getItem('token');
  if(token == null){
    Toast.fail('');
    router.push('/login');
  }else{
    return axios({
      method: "post",
      url,
      data: qs.stringify(data, { allowDots: true }),
      timeout: 5000
    })
  }
}

